.animated-words {
    display: flex;
    justify-content: center;
}

.animated-words span:nth-child(2) {
    -webkit-animation-delay: 2s;
    animation-delay: 2s;
    color: #ffffff;
}
.animated-words span:nth-child(3) {
    -webkit-animation-delay: 4s;
    animation-delay: 4s;
    color: #ffffff;
}
.animated-words span:nth-child(4) {
    -webkit-animation-delay: 6s;
    animation-delay: 6s;
    color: #ffffff;
}
.animated-words span:nth-child(5) {
    -webkit-animation-delay: 8s;
    animation-delay: 8s;
    color: #ffffff;
}


.animated-words span {
    position: absolute;
    opacity: 0;
    overflow: hidden;
    color: #ffffff;
    -webkit-animation: animateWord 10s linear infinite 0s;
    animation: animateWord 10s linear infinite 0s;
}

@-webkit-keyframes animateWord {
    0% { opacity: 0; }
    2% { opacity: 0; -webkit-transform: translateY(30px); }
    5% { opacity: 1; -webkit-transform: translateY(0px);}
    17% { opacity: 1; -webkit-transform: translateY(0px); }
    20% { opacity: 0; -webkit-transform: translateY(-30px); }
    80% { opacity: 0; }
    100% { opacity: 0; }
}

@keyframes animateWord {
    0% { opacity: 0; }
    2% { opacity: 0; -webkit-transform: translateY(30px); }
    5% { opacity: 1; -webkit-transform: translateY(0px);}
    17% { opacity: 1; -webkit-transform: translateY(0px); }
    20% { opacity: 0; -webkit-transform: translateY(-30px); }
    80% { opacity: 0; }
    100% { opacity: 0; }
}

